class ReservedDeck {
  constructor() {
    this.cards = [];
    this.cardsType = null;
  }

  addCards(cards) {
    let startingCard = cards[0];
    let lastCard = this.cards[this.cards.length - 1];
    if (!this.cardsType) this.cardsType = startingCard.cardType;
    if (!this.sameType(cards)) return false;
    if (!lastCard && startingCard.value !== "A") return false;
    if (lastCard && !lastCard.isNextCard(startingCard)) return false;
    this.cards = this.cards.concat(cards);
    return true;
  }

  sameType(cards) {
    const sameTypeCards = cards.filter(card => card.cardType == this.cardsType);
    return cards.length === sameTypeCards.length;
  }

  getCards() {
    return [this.cards[this.cards.length - 1]];
  }

  takeCard() {
    let card = this.cards.pop();
    card.faceUp();
    if (this.cards.length === 0) this.cardsType = null;
    return card;
  }
}

export default ReservedDeck;
