class Pile {
  setCards(cards) {
    this.cards = cards;
    const lastCard = this.cards[cards.length - 1];
    lastCard.faceUp();
  }

  addCards(cards) {
    const lastCard = this.cards[this.cards.length - 1];
    const firstCard = cards[0];
    if (lastCard) {
      if (lastCard.color === firstCard.color) return false;
      if (!lastCard.isPreviousCard(firstCard)) return false;
    } else if (firstCard.value != "K") {
      return false;
    }
    this.cards = this.cards.concat(cards);
    return true;
  }

  getCards(startingCardNo) {
    return this.cards.slice(startingCardNo - 1);
  }

  getLastCard() {
    return this.cards[this.cards.length - 1];
  }

  takeCards(cards) {
    console.log(cards);
    console.log(this.cards);
    let startingCardIndex = this.cards.indexOf(cards[0]);
    if (startingCardIndex != 0) {
      this.cards[startingCardIndex - 1].faceUp();
    }
    return this.cards.splice(startingCardIndex);
  }
}
export default Pile;
