import cardUnicodes from "./cardUnicodes.js";
import React from "react";

import "./game.css";

import Game from "./model/game";
import { cardValues } from "./model/constants.js";

const GAP_BETWEEN_CARD_OVERLAP = 8;
const CARD_HEIGHT = 19;

function Card(props) {
  let color = props.color || "black";
  let top = props.top || 0;
  let height = props.height || CARD_HEIGHT;
  let className = props.className || "";
  top += "vh";
  height += "vh";
  let draggable = props.draggable === undefined || props.draggable === true;
  return (
    <div
      className={"card " + className}
      draggable={draggable}
      onDragStart={props.onDragStart}
      onClick={props.onClick}
      onDoubleClick={props.onDoubleClick}
      style={{ color, top, height }}
      id={props.id}
    >
      {props.Unicode}
    </div>
  );
}

const createPileCard = function(
  card,
  onCardDragStart,
  onCardDoubleClick,
  key,
  { top, height }
) {
  let unicode = cardUnicodes["BACK"][0];
  let draggable = false;
  let color = "black";
  if (!card.isFacingDown()) {
    unicode = getUnicode(card);
    draggable = true;
    color = card.color;
  }
  return (
    <Card
      Unicode={unicode}
      color={color}
      className="pile-card"
      id={key}
      key={key}
      top={top}
      onDragStart={onCardDragStart}
      onDoubleClick={onCardDoubleClick}
      draggable={draggable}
      height={height}
    />
  );
};

function Pile(props) {
  let key = 0;
  let top = -GAP_BETWEEN_CARD_OVERLAP;
  let height = props.cards.length * GAP_BETWEEN_CARD_OVERLAP + CARD_HEIGHT;
  let { onCardDragStart, onCardDoubleClick } = props;
  return (
    <div
      className="pile"
      id={props.id}
      onDragOver={de => de.preventDefault()}
      onDrop={props.onCardDrop}
    >
      {props.cards.map(card => {
        top += GAP_BETWEEN_CARD_OVERLAP;
        height -= GAP_BETWEEN_CARD_OVERLAP;
        key++;
        return createPileCard(card, onCardDragStart, onCardDoubleClick, key, {
          top,
          height
        });
      })}
    </div>
  );
}

function ReservedDeck(props) {
  let key = 1;
  let cardElement = "";
  if (props.card) {
    cardElement = (
      <Card
        Unicode={getUnicode(props.card)}
        onDragStart={props.onCardDragStart}
        color={props.card.color}
        key={key++}
      />
    );
  }
  return (
    <div
      className="reserved-deck card-holder"
      onDragOver={de => de.preventDefault()}
      onDrop={props.onCardDrop}
      id={props.id}
    >
      {cardElement}
    </div>
  );
}

class GameComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = new Game();
    this.state.setup();
    this.draggingCardContainer = null;
    this.draggingCards = [];
  }

  cardDragStart(dragEvent) {
    const containerDiv = dragEvent.currentTarget.parentElement;
    const containerCategory = containerDiv.parentElement.id;
    if (containerCategory === "deck") {
      this.draggingCardContainer = this.state[containerCategory];
      this.draggingCards = [this.draggingCardContainer.getActiveCard()];
      return;
    }
    this.draggingCardContainer = this.state[containerCategory][
      containerDiv.id - 1
    ];
    let draggingCardNo = dragEvent.currentTarget.id;
    this.draggingCards = this.draggingCardContainer.getCards(draggingCardNo);
  }

  cardDrop(dragEvent) {
    const droppingContainerDiv = dragEvent.currentTarget;
    const droppingContainerCategory = droppingContainerDiv.parentElement.id;
    const dropingContainer = this.state[droppingContainerCategory][
      droppingContainerDiv.id - 1
    ];
    const isDropped = dropingContainer.addCards(this.draggingCards);
    if (isDropped) {
      if (this.draggingCardContainer.takeCards)
        this.draggingCardContainer.takeCards(this.draggingCards);
      if (this.draggingCardContainer.takeCard)
        this.draggingCardContainer.takeCard(this.draggingCards[0]);
      this.setState(this.state);
    }
  }

  handleDoubleClick(event) {
    let pileNo = event.currentTarget.parentElement.id;
    let card = this.state.piles[pileNo - 1].getLastCard();
    let isPlaced = false;
    for (let reservedDeck of this.state.reservedDecks) {
      if (reservedDeck.addCards([card])) {
        isPlaced = true;
        break;
      }
    }
    if (!isPlaced) return;
    this.state.piles[pileNo - 1].takeCards([card]);
    this.setState(this.state);
  }

  changeActiveCard() {
    this.state.deck.changeActiveCard();
    this.setState(this.state);
  }

  generateReservedDeckDivs() {
    let reservedDeckDivs = [];
    let key = 1;
    for (let reservedDeck of this.state.reservedDecks) {
      let div = (
        <ReservedDeck
          card={reservedDeck.getCards()[0]}
          id={key}
          key={key++}
          onCardDrop={this.cardDrop.bind(this)}
          onCardDragStart={this.cardDragStart.bind(this)}
        />
      );
      reservedDeckDivs.push(div);
    }
    return reservedDeckDivs;
  }

  generatePileDivs() {
    let pileDivs = [];
    let key = 1;
    for (let pile of this.state.piles) {
      let div = (
        <Pile
          cards={pile.cards}
          id={key}
          onCardDoubleClick={this.handleDoubleClick.bind(this)}
          key={key++}
          onCardDragStart={this.cardDragStart.bind(this)}
          onCardDrop={this.cardDrop.bind(this)}
        />
      );
      pileDivs.push(div);
    }
    return pileDivs;
  }

  render() {
    let activeCard = this.state.deck.getActiveCard();

    let reservedDeckDivs = this.generateReservedDeckDivs();
    let pileDivs = this.generatePileDivs();
    return (
      <div className="game">
        <div className="foundation">
          <div className="deck" id="deck">
            <div className="deck-cards card-holder">
              <Card
                Unicode={cardUnicodes["BACK"][0]}
                draggable={false}
                onClick={this.changeActiveCard.bind(this)}
                key="1"
              />
            </div>
            <div className="card-holder">
              <Card
                Unicode={getUnicode(activeCard)}
                draggable={true}
                color={activeCard.color}
                onDragStart={this.cardDragStart.bind(this)}
                key="2"
              />
            </div>
          </div>

          <div className="deck reservedDecks" id="reservedDecks">
            {reservedDeckDivs}
          </div>
        </div>

        <div className=" tableau">
          <div className="piles" id="piles">
            {pileDivs}
          </div>
        </div>
      </div>
    );
  }
}
function getUnicode(card) {
  let cardValue = cardValues.indexOf(card.value);
  let cardUnicode = cardUnicodes[card.cardType][cardValue];
  return cardUnicode;
}

export default GameComponent;
